export class OraSelect extends Component {

    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this)
        this.handleChangeIe8 = this.handleChangeIe8.bind(this)
        this.handleClick = this.handleClick.bind(this)
    }

    componentDidMount() {
        if(OnlineUtils.ifIe8) {
            //IE8 hack, IE8 cant handle onChange on select properly
            const select = document.getElementById(this.props.id);
            select.attachEvent("onchange",this.handleChangeIe8.bind(this));
        }
    }

    componentDidUpdate() {
        if(this.props.isFeedbackParam) {
        //TEGO TUTAJ NIE POWINNO BYC to jest ogolny komponent widoku!!! takie rzeczy powinny byc obslugiwane w akcjach po kliknieciu!!! ogolnie to cos jest konkretnie spierdolone jak musisz wywolywac po zmianie ponownie event na zmiane
            if(typeof Feedback !== 'undefined') {
                Feedback.showHideFeedback();
            }
        }
    }

    handleChangeIe8(e) {
        const {id, name, value} = e.srcElement;
        this.props.onChange({id, name, value});
    }

    handleChange(event) {
        const {id, name, value} = event.target;
        this.props.onChange({id, name, value});
    }

    handleClick(event) {
        const {id, value} = event.currentTarget.dataset;
        this.props.onChange({id, value});
    }

    render() {
        if(this.props.options.length === 1 && this.props.options[0]) {
            return (
                    <p className={"u-padding-top-m u-padding-left-m "+this.props.singleOptionClassName}>
                        {this.props.singleDataText ? this.props.singleDataText : null}
                        {this.props.options[0].description}
                    </p>
            );
        }
        else {
            if (this.props.selectionType === "TAB") {
                return (
                    <div className="opl-tabs opl-tabs--small opl-tabs--secondary opl-tabs--arrows">
                        {this.props.options.length>1?<div className="opl-tabs__nav-wrapper">
                            <div className="opl-tabs__nav-wrapper-inner opl-tabs__arrows opl-tabs__gradient-right">
                                <ul className="opl-tabs__nav" data-js-module="common/modules/opl-tabs" data-js-options='{"tabNavLinkInnerCor": 30, "arrows": true, "tabArrows": true, "draggable": false}'>
                                    {
                                        this.props.options.map((option, index) =>
                                            <li className="opl-tabs__nav-item">
                                                <div className="opl-tabs__nav-item__content">
                                                    <a href="#" onClick={this.handleClick} data-id={this.props.id + option.value} data-value={option.value} className={"opl-tabs__nav-link " + (option.value === this.props.selected ? "is-active" : "")}
                                                       data-selected={(option.description === this.props.selectedName ? "1" : "0")} aria-selected={option.value === this.props.selected}>
                                                        <span className="opl-tabs__nav-link-inner"><span>{option.description}</span></span>
                                                    </a>
                                                </div>
                                            </li>
                                        )
                                    }
                                </ul>
                            </div>
                        </div>:null}
                    </div>
                )
            } else {
                return (
                    <div className={"o-select " + this.props.className}>
                        {this.props.label ?
                            <label htmlFor={this.props.id} className="u-acc-hide">{this.props.label}</label> : null}
                        <select onChange={this.handleChange} name={this.props.name} value={this.props.selected}
                                id={this.props.id} disabled={this.props.disabled} aria-label={this.props.ariaLabel}
                                data-feedback-param={this.props.isFeedbackParam} className={this.props.selectClassName}>
                            {this.props.withEmptyOption ? <option key={this.props.emptyOption.value}
                                                                  value={this.props.emptyOption.value}>{this.props.emptyOption.description}</option> : null}
                            {this.props.options.map((option) => (
                                <option key={option.value} value={option.value} hidden={option.hidden}
                                        disabled={option.disabled}>
                                    {option.description}
                                </option>
                            ))}
                        </select>
                        <span className="o-select__arrow"/>
                    </div>
                );
            }
        }
    }
}
OraSelect.propTypes = {
    label: PropTypes.string,
    id: PropTypes.string.isRequired,
    isFeedbackParam: PropTypes.any,
    name: PropTypes.string,
    options: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.oneOfType([PropTypes.string,PropTypes.number
        ]),
        description: PropTypes.string
    })),
    selected: PropTypes.oneOfType([PropTypes.string,PropTypes.number
                      ]),
    disabled: PropTypes.bool,
    className: PropTypes.string,
    onChange: PropTypes.func,
    emptyOption: PropTypes.shape({
        value: PropTypes.string,
        description: PropTypes.string
    }),
    withEmptyOption: PropTypes.bool,
    singleDataText: PropTypes.string,
    selectClassName: PropTypes.string,
    singleOptionClassName: PropTypes.string,
    selectionType: PropTypes.string,
    ariaLabel: PropTypes.string
};

OraSelect.defaultProps = {
    emptyOption: {value: "", description: "Wybierz..."},
    withEmptyOption: false,
    selectClassName: "",
    singleOptionClassName: "",
    selectionType: "DROPDOWN",
    ariaLabel: "wybierz z listy"
};

