export class OraFloatingSelect extends Component {

    componentDidMount() {
        loadModule(this.refs.wrapper, {path: "core/modules/floating-label"});
    }

    handleChange(event) {
        let {id, name, value} = event.target;
        this.props.onChange({id, name, value});
    }

    render() {
        let finalOptions = this.props.withEmptyOption ? [this.props.emptyOption].concat(this.props.options) : this.props.options.slice();
        return (
            <div className={"o-floating-label o-select " + this.props.className} ref="wrapper">
                <select className={this.props.btnClassName + (!!this.props.value ? " is-not-empty" : "")} value={this.props.value || ""} id={this.props.id} name={this.props.name} onChange={this.handleChange.bind(this)} aria-describedby="floating-label__placeholder" disabled={this.props.disabled}>
                    {
                        finalOptions.map(o =>
                            <option key={this.props.id + "_" + o.value} value={o.value} disabled={o.disabled} hidden={o.hidden}>
                                {o.description}
                            </option>
                        )
                    }
                </select>
                <span className="o-select__arrow"></span>
                <p className="label o-floating-label__placeholder" id="floating-label__placeholder">{this.props.label}</p>
            </div>
        );
    }
}

