export const OraLabel = (props) => {
    const label = <label htmlFor={props.htmlFor} className={props.className}>{props.children}</label>;
    return (!props.wrapperClassName ? label :
        <div className={props.wrapperClassName}>
            {label}
        </div>
    );
};

OraLabel.propTypes = {
    value: PropTypes.string,
    className: PropTypes.string,
    wrapperClassName: PropTypes.string,
    htmlFor: PropTypes.string,
    children: PropTypes.any
};

OraLabel.defaultProps = {
    value: "",
    className: "",
    wrapperClassName: "",
    htmlFor: ""
};


export class OraInput extends PureComponent {
    constructor(props) {
        super(props);
        this.state={value:""};
        this.handleChange = this.handleChange.bind(this);
        this.handleBlur = this.handleBlur.bind(this);
    }

    handleChange(event) {
        this.setState({value:event.target.value})
        if(!!this.props.onChange) {
            let {id, name, value} = event.target;
            this.props.onChange({id, name, value});
        }
    }

    handleBlur(event) {
        if(!!this.props.onBlur) {
            let {id, name, value} = event.target;
            this.props.onBlur({id, name, value});
        }
    }

    componentDidUpdate(prevProps){
        if(!prevProps.focusOnMount && this.props.focusOnMount){
            $(this.refs.inputRef).focus();

        }
    }
    componentDidMount() {

        //Very ugly workaround... Do not duplicate!
        if(!!this.props.browserAutocomplete) {
            ReactDOM.findDOMNode(this.refs.inputRef).setAttribute('browserautocomplete', this.props.browserAutocomplete);
        }

        console.log("INPUT MOUNTED ",this.props.id);

        if(this.props.focusOnMount){
            let that=this;
            setTimeout(function () {
                if(that.refs.inputRef) {
                    that.refs.inputRef.focus();
                }
            },500);
        }
        if(this.props.autocomplete) {
            OPL.UI.loadModules(this.refs.inputRef, {
                path: "common/modules/opl-autocomplete",
                options: {
                    sortElements: false,
                    url: this.props.autocompleteUrl,
                    type: 'POST',
                    responsdatavalue: 'id',
                    responsdatalabel: 'name',
                    noWrap: true,
                    completecallback: this.props.onPick,
                    data: this.props.data
                }
            });
            OPL.UI.on('module.started', function (data) {
                if (data.module === 'common/modules/opl-autocomplete') {
                    console.log("Autocomplete ready ", data);
                    if (!!data.element.getAttribute('browserautocomplete')) {
                        data.element.autocomplete = data.element.getAttribute('browserautocomplete');
                    }
                }
            })
        }
    }





    render() {
        let classNames = this.props.className;
        if (this.props.value && this.props.value.length > 0) {
            classNames += ' is-not-empty';
        }
        const input = (
            <input
                {...excludeProps(this.props, ["wrapperClassName", "labelType", "validateEmpty", "browserAutocomplete", "focusOnMount", "autocompleteUrl", "onPick", "autocomplete"])}
                className={classNames}
                ref="inputRef"
                onChange={this.handleChange}
                onBlur={this.handleBlur}
                onFocus={this.props.onFocus}
                autoComplete={this.props.browserAutocomplete}
                onKeyUp={this.props.onKeyUp}
            />
        );
        return (!this.props.wrapperClassName ? input :
            <div className={this.props.wrapperClassName}>
                {input}
            </div>
        );
    }
}

OraInput.propTypes = {
    id: PropTypes.string,
    name: PropTypes.string,
    className: PropTypes.string,
    wrapperClassName: PropTypes.string,
    placeholder: PropTypes.string,
    value: PropTypes.string,
    type: PropTypes.string,
    maxLength: PropTypes.string,
    onChange: PropTypes.func,
    onBlur: PropTypes.func,
    browserAutocomplete: PropTypes.string
};

OraInput.defaultProps = {
    className: "",
    wrapperClassName: "",
    placeholder: "",
    type: "text",
    browserAutocomplete:"on"
};

