const PriceType = PropTypes.shape({
    currency: PropTypes.string,
    gross: PropTypes.string,
    net: PropTypes.string,
    monthFrom: PropTypes.number,
    monthTo: PropTypes.number
});

const priceNotEmptyRE = /[1-9]/;

export class OraPrice extends Component {
    isEmpty() {
        return !this.props.price || !this.props.price.gross || !priceNotEmptyRE.test(this.props.price.gross);
    }

    getPrice(price) {
        return price + (this.props.price.currency && this.props.price.currency !== "" ? " " + this.props.price.currency : "");
    }

    render() {
        const empty = this.isEmpty(),
            className = [this.props.className, empty ? this.props.classEmpty : this.props.classFilled].join(" ");
        return (
            <span className={className}>
                {!empty ? this.getPrice(this.props.price.gross) : this.props.filler}
                {(empty || !this.props.showNet) ? null :
                    <span className={this.props.classNet}>({this.getPrice(this.props.price.net)} netto)</span>}
            </span>
        );
    }
}

OraPrice.propTypes = {
    price: PriceType,
    className: PropTypes.string,
    classFilled: PropTypes.string,
    classEmpty: PropTypes.string,
    filler: PropTypes.string,
    showNet: PropTypes.bool,
    classNet: PropTypes.string
};

OraPrice.defaultProps = {
    className: "",
    classFilled: "",
    classEmpty: "",
    filler: "",
    showNet: false,
    classNet: ""
};


export class OraTieredPrice extends Component {
    isTiered() {
        return this.props.price && (this.props.price.length > 1
        || (this.props.price.length === 1 && _.first(this.props.price).monthTo < this.props.loyaltyLength));
    }

    getMonthDesc(price) {
        return price.monthTo ? this.getMonthDescWithEndCycle(price) : this.getMonthDescUndefinedEndCycle(price);
    }

    getMonthDescWithEndCycle(price) {
        return this.props.monthDesc + " " + price.monthFrom
            + (price.monthTo !== price.monthFrom ? "-" + price.monthTo : "")
            + ":";
    }

    getMonthDescUndefinedEndCycle(price) {
        return this.props.mothFromDesc.replace(/%MONTH%/g, price.monthFrom);
    }

    render() {
        return !this.isTiered() ? <OraPrice {...this.props} price={this.props.price && this.props.price[0]}/> :
            <ul>
                {this.props.price.map(price =>
                    <li key={price.monthFrom + "-" + price.monthTo} className="opl-table__list-item">
                        <span className={this.props.classMonth}>
                            {this.getMonthDesc(price)}
                        </span>
                        <OraPrice {...this.props} price={price}/>
                    </li>
                )}
            </ul>

    }
}

OraTieredPrice.propTypes = {
    price: PropTypes.arrayOf(PriceType),
    className: PropTypes.string,
    classMonth: PropTypes.string,
    monthDesc: PropTypes.string,
    mothFromDesc: PropTypes.string,
    loyaltyLength: PropTypes.number
    // Also accepts all valid props for OraPrice
};

OraTieredPrice.defaultProps = {
    className: "",
    classMonth: "",
    monthDesc: "Miesic",
    mothFromDesc: "Od %MONTH% miesica:"
};

