export var OraDatePicker = React.createClass({
    propTypes: {
        id: PropTypes.string.isRequired,
        value: PropTypes.string,
        onChange: PropTypes.func,
        icon: PropTypes.string,
        floatingLabel: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
        inputClassName: PropTypes.string,
        // opl-datepicker options
        options: PropTypes.shape({
            disabledDates: PropTypes.array,
            datepicker: PropTypes.bool,
            timepicker: PropTypes.bool,
            minDate: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
            maxDate: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
            format: PropTypes.string,
            formatDate: PropTypes.string,
            step: PropTypes.number,
            startDate: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
            defaultDate: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
            defaultTime: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
            disabledWeekDays: PropTypes.arrayOf(PropTypes.number),
            allowDates: PropTypes.arrayOf(PropTypes.string),
            allowTimes: PropTypes.arrayOf(PropTypes.string),
            hideOtherMonth: PropTypes.bool,
            scrollInput: PropTypes.bool
        })
    },

    getDefaultProps() {
        return {
            icon: "calendar3",
            floatingLabel: false,
            options: {}
        };
    },

    getDatepickerOptions(props) {
        // overriding hasIcon because it's modifying DOM, we'd rather do that ourselves
        return Object.assign({hasIcon: false}, props.options);
    },

    componentDidMount: function () {
        OPL.UI.on(OPL.UI.EVENTS.modules.datepicker.selectedDate, this.onChangeDate, this.props.id);
        OPL.UI.loadModules(this.refs.datepicker, [{
            path: 'common/modules/opl-datepicker',
            options: this.getDatepickerOptions(this.props)
        }]);
    },

    componentWillUnmount: function () {
        OPL.UI.off(OPL.UI.EVENTS.modules.datepicker.selectedDate, this.onChangeDate, this.props.id);
    },

    componentWillReceiveProps: function (newProps) {
        for (const opt in newProps.options) {
            if (this.props.options[opt] !== newProps.options[opt]) {
                OPL.UI.fire(OPL.UI.EVENTS.modules.datepicker.loaddata, this.getDatepickerOptions(newProps), this.props.id);
                break;
            }
        }
    },

    onChangeDate: function (date) {
        if (this.props.onChange) {
            this.props.onChange({id: this.props.id, name: this.props.name, value: this.refs.datepicker.value});
        }
    },

    onChangeValue: function (event) {
        let {id, name, value} = event.target;
        if (this.props.onChange) {
            this.props.onChange({id, name, value});
        }
    },

    renderContent: function () {
        return ([
            <input type="text" id={this.props.id} value={this.props.value} ref="datepicker" onChange={this.onChangeValue} className={this.props.inputClassName} />,
            !this.props.icon ||
            <label htmlFor={this.props.id} className={"calendar-icon g-icon g-icon--" + this.props.icon}>
                <span className="u-acc-hide">Poka kalendarz</span>
            </label>
        ]);
    },

    render: function () {
        return (
            <div className="opl-datepicker">
                { this.props.floatingLabel ?
                    <FloatingLabelForDatePicker {...this.props}>
                        { this.renderContent() }
                    </FloatingLabelForDatePicker>
                :
                    this.renderContent()
                }
            </div>
        );
    }
});

